
import { Checklist,Toast } from 'mint-ui';
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'


export default{
name:"register",
components:{
Toast,Checklist,XInput, Group,XHeader
},
data(){
return {
click_state:true,
is_setBtn:false,
getBtn:"获取",
time:60,
yzm:"",
tel:"",
psw:"",
yqm:"",
confirm_psw:"",
clause:"1",
is_r:true,
title:"注册",
}
},
methods:{

reg_fn(){
if(!this.yzm){
Toast("请输入验证码");
return false;
}else if(!this.tel){
Toast("请输入手机号");
return false;
}else if(!this.psw){
Toast("请输入密码");
return false;
}else if(this.psw!=this.confirm_psw){
Toast("您输入的两次密码不一致");
return false;
}else if(!this.clause){
Toast("请勾选隐私政策和服务条款");
return false
}

this.$store.dispatch("register", {
data: "data="+JSON.stringify({
mobile:this.tel,
code:this.yzm,
password:this.confirm_psw,
agree:this.clause,
invite_code:this.yqm
}),
fn: (code, msg) => {
switch (code) {
case 200:
this.$router.push("/my");
break;
case -1:
Toast(msg)
break;
default:
Toast("请求出错")
break;
}
}
})
},

getCode(){
if(this.tel==""){
Toast("请输入手机号码")
return false;
}else if(!(/^1[3578]\d{9}$/.test(this.tel))){
Toast("您输入的手机号码有误");
return false;
}

if(this.click_state){
this.click_state = false;
this.$store.dispatch("verificationCode",{
data:`data=${JSON.stringify({mobile:this.tel,type:"REGISTER"})}`,
fn:(code,msg)=>{
switch (code) {
case 200:

this.countDown(msg.limit_time);
Toast("验证码获取成功");
break;
case -1:
Toast(msg);
this.click_state = true;
break;
default:
Toast("请求出错");
this.click_state = true;
break;
}
}
});
}
},
zeroFill(time){
return time<10?`0${time}`:time;
},
countDown(time){
setTimeout(()=>{
--time;
if(time<1){
time = 60;
this.getBtn = "重新获取";
this.is_setBtn = false;
this.click_state = true;
}else{
this.is_setBtn = true;
this.getBtn = `${this.zeroFill(time)}S 后可重新获取`;
this.countDown(time);
}
},1000);
},
target_handle(e){

e.stopPropagation();
e.preventDefault();
return false;
}
}
}



